header {
	height: 100vh;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

.header__container h1 {
	font-size: 4rem;
}

.header__container h5 {
	font-size: 1rem;
}

/* ===================== CTA ====================== */
.cta {
	margin-top: 2.5rem;
	display: flex;
	gap: 1.2rem;
	justify-content: center;
}

/* ===================== HEADER SOCIALS ====================== */
.header_socials {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.8rem;
	position: absolute;
	left: 0;
	bottom: 3rem;
}

.header_socials::after {
	content: '';
	width: 1px;
	height: 2rem;
	background: var(--color-primary-variant);
}

/* ===================== ME ====================== */
.me {
	background: linear-gradient(var(--color-bg-variant), transparent);
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 50%;
	height: 50rem;
	/* position: absolute; */
	/* left: calc(50% - 11rem); */
	margin-top: 4rem;
	border-radius: 12rem 12rem 0 0;
	overflow: hidden;
	padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ===================== SCROLL DOWN ====================== */
.scroll_down {
	position: absolute;
	right: -2.3rem;
	bottom: 5rem;
	transform: rotate(90deg);
	font-weight: 300;
	font-size: 0.9rem;
}

/* ========================== MEDIA QUERIES (MEDIUM DEVICES) ============================== */
@media screen and (max-width: 1024px) {
	header {
		height: 65vh;
	}
}
/* ========================== MEDIA QUERIES (SMALL DEVICES) ============================== */
@media screen and (max-width: 600px) and (max-height: 900px) {
	header {
		height: 90vh;
	}

	.header_socials,
	.scroll_down {
		display: none;
	}
}

/* For IPhone SE Size (375 x 667) & Samsung Galaxy S8+ (360 x 740) */
@media screen and (max-width: 425px) and (max-height: 750px) {
	header {
		height: 120vh;
	}

	.header_socials,
	.scroll_down {
		display: none;
	}
}

/* For Samsung Galaxy S20 Ultra (412 x 915)*/
@media screen and (max-width: 425px) and (min-height: 900px) {
	header {
		height: 90vh;
	}

	.header_socials,
	.scroll_down {
		display: none;
	}
}
